.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(3px);
  display: flex;
  justify-content: flex-start;
  align-items: center;
  z-index: 1000;
}

.modal-box {
  background: #fff;
  padding: 30px;
  margin-left: 5%;
  border-radius: 16px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3);
  width: 350px;
  animation: slideInLeft 0.4s ease-out;
}

@keyframes slideInLeft {
  from {
    transform: translateX(-40px);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

.modal-title {
  font-size: 1.6rem;
  font-weight: bold;
  margin-bottom: 20px;
  text-align: left;
}

.modal-box input,
.modal-box textarea {
  width: 90%;
  padding: 12px 14px;
  margin-bottom: 14px;
  border: 1px solid #ccc;
  border-radius: 10px;
  font-size: 1rem;
}

.modal-box textarea {
  height: 90px;
  resize: vertical;
}

.modal-buttons {
  display: flex;
  gap: 10px;
}

.submit-btn,
.cancel-btn {
  flex: 1;
  padding: 10px 14px;
  font-size: 1rem;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.3s;
}

.submit-btn {
  background-color: #4a90e2;
  color: white;
}

.submit-btn:hover {
  background-color: #3b7dc7;
}

.cancel-btn {
  background-color: #e0e0e0;
  color: #333;
}

.cancel-btn:hover {
  background-color: #bbb;
}
