/* Profile.css */

.profile-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 75vh;
  background-color: #f3f4f6;
}

.profile-card {
  background: white;
  padding: 2rem;
  border-radius: 12px;
  box-shadow: 0 5px 25px rgba(0, 0, 0, 0.1);
  text-align: center;
  width: 320px;
}

.profile-avatar {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  object-fit: cover;
  margin-bottom: 1rem;
}

.profile-name {
  font-size: 1.5rem;
  font-weight: 600;
  color: #333;
}

.profile-email {
  color: #666;
  margin-bottom: 1.5rem;
}

.logout-btn {
  background-color: #ff4d4f;
  color: white;
  padding: 10px 24px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.2s ease-in-out;
}

.logout-btn:hover {
  background-color: #d9363e;
}
