/* PetConnect Unique CSS Theme */

/* Global Styles */
body {
    margin: 0;
    font-family: 'Poppins', sans-serif;
    background: #fdfdfd;
    color: #333;
    line-height: 1.6;
  }
  
  a {
    text-decoration: none;
    color: #5563DE;
  }
  
  h1, h2, h3 {
    font-family: 'Pacifico', cursive;
    color: #2D3142;
  }
  
  .container {
    max-width: 1200px;
    margin: 2rem auto;
    padding: 2rem;
    background: white;
    border-radius: 16px;
    box-shadow: 0 15px 30px rgba(0,0,0,0.05);
  }
  
  /* Header */
  header {
    background: linear-gradient(135deg, #74ABE2, #5563DE);
    padding: 1.5rem;
    text-align: center;
    color: white;
    box-shadow: 0 4px 10px rgba(0,0,0,0.1);
    border-bottom: 6px solid #ffe66d;
    position: sticky;
    top: 0;
    z-index: 999;
  }
  
  header h1 {
    margin: 0;
    font-size: 2.5rem;
  }
  
  header nav {
    margin-top: 1rem;
  }
  
  header nav a {
    margin: 0 1rem;
    color: #ffe;
    font-weight: bold;
    font-size: 1.1rem;
    transition: color 0.3s;
  }
  
  header nav a:hover {
    color: #ffe66d;
  }
  
  /* Footer */
  footer {
    background-color: #f4f4f4;
    text-align: center;
    padding: 1rem;
    margin-top: 3rem;
    font-size: 0.9rem;
    border-top: 2px solid #eee;
  }
  
  /* Form styling */
  form {
    max-width: 500px;
    margin: auto;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    padding: 2rem;
    background-color: #f8f8ff;
    border-radius: 12px;
    box-shadow: 0 4px 20px rgba(0,0,0,0.1);
  }
  
  form input,
  form textarea {
    padding: 0.75rem 1rem;
    border: 1px solid #ccc;
    border-radius: 8px;
    font-size: 1rem;
  }
  
  form button {
    background: #5563DE;
    color: white;
    border: none;
    padding: 0.75rem;
    font-size: 1rem;
    border-radius: 8px;
    cursor: pointer;
    transition: background 0.3s ease;
  }
  
  form button:hover {
    background: #2e40aa;
  }
  
  /* Pet Card styling */
  .pet-card {
    display: flex;
    flex-direction: column;
    border: 2px solid #eee;
    padding: 1.5rem;
    margin: 1rem 0;
    border-radius: 12px;
    transition: all 0.3s ease;
    box-shadow: 0 6px 10px rgba(0,0,0,0.05);
    background: linear-gradient(45deg, #ffffff, #f8faff);
  }
  
  .pet-card:hover {
    transform: scale(1.02);
    border-color: #a7c7e7;
  }
  
  .pet-card img {
    max-width: 100%;
    border-radius: 8px;
    margin-top: 1rem;
  }

  form {
    margin-top: 20px;
    padding: 16px;
    border: 1px solid #eee;
    border-radius: 8px;
    background: #f9f9f9;
  }
  form h3 {
    margin-bottom: 10px;
  }
  form textarea {
    width: 100%;
    font-family: inherit;
  }
  
  
  /* Review Card */
  .review {
    border-left: 4px solid #5563DE;
    background: #f9f9f9;
    margin: 1rem 0;
    padding: 1rem;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0,0,0,0.05);
  }
  
  .review p {
    margin: 0.5rem 0;
  }

  .comment-box {
  width: 100%;
  max-width: 470px;
  height: 120px;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 8px;
  resize: vertical; /* Allows manual resizing */
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.1);
}

  
  /* Responsive Design */
  @media (max-width: 768px) {
    .container {
      margin: 1rem;
      padding: 1rem;
    }
    header nav {
      display: flex;
      flex-direction: column;
      gap: 0.5rem;
    }
    header nav a {
      font-size: 1rem;
    }
  }
  